/**
 Smoothly scroll element to the given target (element.scrollTop)
 for the given duration

 Returns a promise that's fulfilled when done, or rejected if
 interrupted
 */
export default function(element, offset, duration) {
  duration = Math.round(duration);
  if (duration < 0) {
    return Promise.reject("bad duration");
  }
  if (duration === 0) {
    element.scrollTop += offset.top;
    element.scrollLeft += offset.left;
    return Promise.resolve();
  }
  if(offset.left === 0 && offset.top === 0){
    return Promise.resolve();
  }

  const start_time = Date.now();
  const end_time = start_time + duration;

  const start_top = element.scrollTop;
  const start_left = element.scrollLeft;
  const distance_top = offset.top;
  const distance_left = offset.left;
  const distance = Math.abs(distance_top) > Math.abs(distance_left) ? distance_top : distance_left;

  // based on http://en.wikipedia.org/wiki/Smoothstep
  var smooth_step = function(start, end, point) {
    if(point <= start) { return 0; }
    if(point >= end) { return 1; }
    const x = (point - start) / (end - start); // interpolation
    return x*x*(3 - 2*x);
  }

  return new Promise(function(resolve, reject) {
    // This is to keep track of where the element's scrollTop is
    // supposed to be, based on what we're doing
    let previous_top = element.scrollTop;
    let previous_left = element.scrollLeft;

    // This is like a think function from a game loop
    const scroll_frame = function() {

      if(element.scrollTop != previous_top || element.scrollLeft != previous_left) {
        reject("interrupted");
        return;
      }

      // set the scrollTop for this frame
      const now = Date.now();
      const point = smooth_step(start_time, end_time, now);
      const frameTop = Math.round(start_top + (distance * point));
      const frameLeft = Math.round(start_left + (distance * point));
      element.scrollTop = frameTop;
      element.scrollLeft = frameLeft;

      // check if we're done!
      if(now >= end_time) {
        resolve();
        return;
      }

      // If we were supposed to scroll but didn't, then we
      // probably hit the limit, so consider it done; not
      // interrupted.
      if(element.scrollTop === previous_top
        && element.scrollTop !== frameTop
        && element.scrollLeft === previous_left
        && element.scrollLeft !== frameLeft ) {
        resolve();
        return;
      }
      previous_top = element.scrollTop;
      previous_left = element.scrollLeft;

      // schedule next frame for execution
      window.requestAnimationFrame(scroll_frame);
    }

    // boostrap the animation process
    window.requestAnimationFrame(scroll_frame);
  });
}



