<template>
  <div>
    <h2 v-translate>Enrollment</h2>
    <form v-if="login && isEnrollChecked" @submit.prevent="submitAction" id="enroll-form" name="enroll-form" class="enroll-form forms">
      <div v-if="isAlreadyEnrolled">
        <p><span v-translate>You have been already enroll on this event!</span></p>
      </div>
      <div v-else>
        <div v-if="formSubmitted">
          <h3 v-translate>Congratulation</h3>
          <p><span v-translate>Successfully</span> {{event.title}}</p>
        </div>
        <div v-else>
          <button type="submit" class="button-primary" v-bind:disabled="formSubmitted" v-translate>Enroll</button>
        </div>
      </div>
    </form>
    <div v-if="!login">
      <div v-if="hideRegister">
        <login-form></login-form>
        <button @click="hideRegister = !hideRegister" class="sign-in" v-translate>Register</button>
      </div>
      <div v-if="!hideRegister">
        <register-form></register-form>
        <button @click="hideRegister = !hideRegister" class="sign-in" v-translate>Login</button>
      </div>
    </div>
  </div>
</template>

<style>
  .modal-container form.enroll-form {
    padding: 0;
    margin: 0;
  }

  .event .modal-container .form {
    padding: 0;
  }

  .event .modal-container .form .col-full input,
  .event .modal-container .form .col-1,
  .event .modal-container .form .col-2 {
    margin-bottom: 1.2rem;
  }

  .event .modal-container .form label.label-inline {
    position: relative;
    top: 9px;
  }
</style>

<script>
  import {User} from '../helpers/Db'
  import LoginForm from './LoginForm'
  import RegisterForm from './RegisterForm'

  export default {
    props: ['event'],
    components: {
      LoginForm,
      RegisterForm
    },
    data: function () {
      return {
        isEnrollChecked: false,
        isAlreadyEnrolled: true,
        mail: '',
        formSubmitted: false,
        hideRegister: true
      }
    },
    computed: {
      routeParams (){
        return this.$store.state.route.params;
      },
      login() {
        return !!this.$store.state.user.username;
      }
    },
    mounted() {
      this.isEnrollChecked = false;
      User.isUserEnroll({
        userId: this.$store.state.user.id,
        eventId: this.event.id
      })
        .then((data) => {
          this.isEnrollChecked = true;
          this.isAlreadyEnrolled = !!data.exists;
        });
    },
    methods: {
      submitAction(e)
      {
        this.$store.dispatch('saveSubscription', {
          event_id: this.event.id,
          user_id: this.$store.state.user.id
        })
          .then(() => {
            this.formSubmitted = true;
          })
      }
    }
  }
</script>



