<template>
  <div class="calendar-wrapper">
    <div class="calendar">
      <calendar-navigation v-bind:date="currentDate" v-on:navigate="navigateByRoute"></calendar-navigation>
      <main>
        <div class="calendar_titles clearfix">
          <b v-for="value in daysLabels"><i>{{value}}</i></b>
        </div>
        <div class="calendar_days clearfix">
          <calendar-td v-for="value in calendarData" v-bind:event="value"></calendar-td>
        </div>
      </main>
    </div>
  </div>
</template>

<script>
  import router from '../../router';
  import fecha from 'fecha';
  import reduce from 'lodash/fp/reduce';
  import min from 'lodash/fp/min';
  import max from 'lodash/fp/max';
  import CalendarNavigation from './CalendarNavigation';
  import CalendarTd from './CalendarTd';

  const getTimelineOfEvents = reduce((result, timestampKey) => {
    let key = fecha.format(new Date(parseInt(timestampKey, 10)), 'YYYY-MM-DD');
    result[key] ? result[key] += 1 : result[key] = 1;
    return result;
  }, {});

  export default {
    data: function () {
      return {
        daysLabels: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
        daysInMonth: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
        checked: false
      }
    },
    components: {
      CalendarTd,
      CalendarNavigation
    },
    computed: {
      currentDate: function () {
        return new Date(this.$store.state.route.params.date)
      },
      currentDay: function () {
        return this.currentDate.getDay();
      },
      currentMonth: function () {
        return this.currentDate.getMonth();
      },
      currentYear: function () {
        return this.currentDate.getFullYear();
      },
      daysInNextMonth: function () {
        return this.daysInMonth[(this.currentMonth + 1) % 12]
      },
      firstDayInCurrentMonth: function () {
        return new Date(this.currentYear, this.currentMonth, 1).getDay();
      },
      lastDayInCurrentMonth: function () {
        return new Date(this.currentYear, this.currentMonth + 1, 0).getDay();
      },
      calendarData: function () {
        const currentDateKey = fecha.format(this.currentDate, 'YYYY-MM-DD');
        const prevMonthDate = new Date(this.currentYear, this.currentMonth);
        const nextMonthDate = new Date(this.currentYear, this.currentMonth + 1);

        let start = new Date(this.currentYear, this.currentMonth, 1 - this.firstDayInCurrentMonth);
        let end = new Date(this.currentYear, this.currentMonth + 1, 6 - this.lastDayInCurrentMonth, 23, 59, 59);

        let calendarObj = [];

        while (start < end) {
          let dateKey = fecha.format(start, 'YYYY-MM-DD');

          calendarObj.push({
            day: fecha.format(start, 'D'),
            date: dateKey,
            prevClass: prevMonthDate > start,
            nextClass: nextMonthDate < start,
            activeClass: dateKey === this.$store.state.route.params.date,
            currentClass: dateKey === currentDateKey,
            events: this.events[dateKey] || false
          });

          let newDate = new Date(start);
          newDate.setDate(newDate.getDate() + 1);
          start = newDate;
        }
        return calendarObj;
      },
      events (){
        return getTimelineOfEvents(this.$store.getters.eventDates);
      }
    },
    methods: {
      navigateByRoute(date) {
        if (!date) {
          return;
        }
        return router.push({name: 'date', params: Object.assign({}, {date: fecha.format(+date, 'YYYY-MM-DD')})});
      },
      loadEventsForCalendar(startTime, endTime) {
        this.$store.dispatch('loadFlatEvents', {
          start: startTime,
          end: endTime
        });
      }
    },
    watch: {
      currentDate (date) {
        if (!date) {
          return;
        }

        const earliestEvent = min(this.$store.getters.eventDates);
        const latestEvent = max(this.$store.getters.eventDates);
        const dateYearBack = new Date(date.getFullYear() - 1, date.getMonth(), 1).getTime();
        const dateYearForward = new Date(date.getFullYear() + 1, date.getMonth(), 1).getTime();

        if (dateYearBack < earliestEvent) {
          return this.loadEventsForCalendar(dateYearBack, earliestEvent);
        }

        if (dateYearForward > latestEvent) {
          return this.loadEventsForCalendar(latestEvent, dateYearForward);
        }
      }
    },
    mounted () {
//      this.loadEventsForCalendar();
    }
  }
</script>

<style>
  @import "variables.css";

  .calendar-wrapper {
    text-align: center;
    border: none;
    margin: 0;
    background: #fff;
    overflow: hidden;
  }

  .calendar {
    text-align: center;
    overflow: hidden;
    padding: 0 0 0.2rem 0;
  }

  .calendar_titles > b {
    display: inline-block;
    font-weight: normal;
    text-transform: uppercase;
    color: #5e6066;
    border: 1px solid transparent;
    text-align: center;
    font-size: 9px;
    width: 13%;
  }

  .calendar_titles > b > i {
    font-style: normal;
  }

  .calendar_days {
    color: #7c8a95;
  }

  .calendar_days > span {
    display: inline-block;
    width: 13%;
    color: #000;
    text-align: center;
  }

  @media (--timeline-small) {
    .calendar-wrapper {
      margin: 2rem auto 0;
    }

    .calendar {
      border: 0;
      width: 100%;
      padding: 0 0 0.5rem 0;
    }

    .calendar_header h2 {
      font-size: 2.7rem;
    }

    .calendar_titles > b {
      width: 13.5%;
      font-size: 11px;
    }

    .calendar_day > a, .calendar_day > span {
      font-size: 14px !important;
      padding: 1.2rem 0 !important;
    }
  }
</style>



