<template>
  <header class="calendar_header">
    <h2>{{monthTitle}} <span>{{year}}</span></h2>
    <button v-bind:disabled="!eventBefore || isDisabledPrev" @click="goPrevious" class="btn-prev">◀</button>
    <button v-bind:disabled="!eventAfter || isDisabledAfter" @click="goNext" class="btn-next">▶</button>
  </header>
</template>


<script>
  import fecha from 'fecha';
  import filter from 'lodash/fp/filter';
  import min from 'lodash/fp/min';
  import max from 'lodash/fp/max';

  export default {
    props: ['date'],
    data () {
      return {
        isDisabledPrev: false,
        isDisabledAfter: false
      }
    },
    computed: {
      monthTitle: function () {
        return fecha.format(this.date, 'MMMM'); // Month
      },
      year: function () {
        return this.date.getFullYear();
      },
      month: function () {
        return this.date.getMonth();
      },
      eventBefore (){
        const prevMonth = new Date(this.year, this.month, 1).getTime();
        return max(filter((event) => {
          return event < prevMonth;
        })(this.$store.getters.eventDates));
      },
      eventAfter (){
        const nextMonth = new Date(this.year, this.month + 1, 1).getTime();
        return min(filter((event) => {
          return event > nextMonth;
        })(this.$store.getters.eventDates));
      }
    },
    methods: {
      navigateTo(date) {
        this.$emit('navigate', date);
      },
      goPrevious: function () {
        this.navigateTo(this.eventBefore);
        this.isDisabledPrev = true;
        setTimeout(() => {
          this.isDisabledPrev = false;
        }, 500)
      },
      goNext: function () {
        this.navigateTo(this.eventAfter);
        this.isDisabledAfter = true;
        setTimeout(() => {
          this.isDisabledAfter = false;
        }, 500)
      }
    }
  }
</script>

<style>
  .calendar_header {
    position: relative
  }

  .calendar_header button {
    text-decoration: none;
    height: 2em;
    font-size: .75em;
    line-height: 2em;
    margin: -1em 0 0 0;
    padding: 0 1rem;
    position: absolute;
    top: 54%;
    width: 1em;
    color: #16407d;
    background: none;
    transition: color .1s linear;
  }

  .calendar_header button[disabled] {
    color: #ddd;
  }

  .calendar_header button:not([disabled]):hover {
    color: #000
  }

  .calendar_header h2 {
    font-size: 1.7rem;
    margin-bottom: 0;
    margin-top: .5rem;
  }

  .calendar_header h2 span {
    font-weight: normal
  }

  .calendar_header .btn-prev, .calendar_header .btn-next {
  }

  .calendar_header .btn-prev {
    left: 0
  }

  .calendar_header .btn-next {
    right: 10px;
  }
</style>



