<style>
  @import "variables.css";

  .in-viewport {
    height:0;
    width:0;
  }

  @media (--timeline-large) {
    .in-viewport {
      height:calc(100vh - var(--headerHeightLg));
    }
  }

  @media (--header-medium) {
    .in-viewport {
      height:calc(100vh - var(--headerHeightMd));
    }
  }

  @media (--timeline-small) {
    .in-viewport {
      width:100%;
    }
  }
</style><script>
  import throttle from 'lodash/throttle'
  import isInViewport from '../helpers/in-viewport'

  const triggers = ['scroll', 'resize', 'update']

  const MugenScroll = {
    name: 'in-viewport',
    props: {
      handler: {
        type: Function,
        required: true
      },
      shouldHandle: {
        type: Boolean,
        default: true
      },
      handleOnMount: {
        type: Boolean,
        default: true
      },
      scrollContainer: {
        type: HTMLElement,
        required: true
      }
    },
    mounted() {
      this.checkInView()
    },
    methods: {
      checkInView() {
        const execute = (e) => {
          const inView = isInViewport(this.scrollContainer, this.$refs.scroll, 200);
          if (this.shouldHandle && inView) {
            this.handler()
          }
        }

        // checkInView right after this component is mounted
        if (this.handleOnMount) execute()

        this.check = throttle(execute, 200, {trailing: true, leading: false});
        triggers.forEach(event => this.scrollContainer.addEventListener(event, this.check))
      }
    },
    render(h) {
      return h('div', {
        staticClass: 'in-viewport',
        ref: 'scroll'
      }, this.$slots.default)
    },
    beforeDestroy() {
      triggers.forEach(event => this.scrollContainer.removeEventListener(event, this.check))
    }
  }

  export default MugenScroll
</script>



