<template>
  <span class="current-date" v-bind:style="currentDateStyle"><i>{{currentDate}}</i></span>
</template>

<style>
  @import "variables.css";

  .current-date {
    display: block;
    position: absolute;
    top: 1.05rem;
    left: 0;
    z-index: 1;
    color: #e73e42;
    font-size: 1rem;
    line-height:1rem;
    white-space: nowrap;
    padding:.1rem .2rem;
  }
  .current-date:before {
    background: #e73e42;
    content: '';
    height: .9rem;
    width: .9rem;
    border: none;
    box-shadow: 0 0 1px #fff;
    display: block;
    border-radius: 50%;
    position: relative;
    z-index:1;
  }
  .current-date i {
    font-style: normal;
    background: rgba(255,255,255,.9);
    padding:0 .2rem;
    margin:0 0 0 -.2rem;
  }
</style>

<script>
  import fecha from 'fecha';
  import throttle from 'lodash/throttle'
  import weekNumber from '../helpers/week-number'

  function getPositionSide(htmlEl) {
    return (htmlEl < 800) ? 'top' : 'left'
  }

  export default {
    props: ['currentDatePosition'],
    data: function () {
      return {
        positionSide: getPositionSide(window.innerWidth)
      }
    },
    mounted: function () {
      const handlerResize = (e) => {
        this.positionSide = getPositionSide(e.currentTarget.innerWidth)
      }

      this.handlerResizeThrottled = throttle(handlerResize, 300, {
        leading: false,
        trailing: true
      });

      window.addEventListener('resize', this.handlerResizeThrottled);
    },
    beforeDestroy: function () {
      window.removeEventListener('resize', this.handlerResizeThrottled);
    },
    computed: {
      currentDateStyle () {
        let elStyle = {};
        elStyle[this.positionSide] = this['getPosition_'+this.positionSide](this.currentDatePosition);
        return elStyle;
      },
      currentDate () {
        return fecha.format(this.$store.state.currentDate, 'MMM DD');
      }
    },
    methods: {
      getPosition_left(data) {
        const position = data*19;
        return (position > -3 ? -3 : position ) + 'rem';
      },
      getPosition_top(data) {
        const position = data*12;
        return (position > -2 ? -2 : position ) + 'rem';
      }
    }
  }

</script>



