<template>
  <div class="form">
    <form v-on:submit.prevent id="form-register" name="form-register">
      <ul v-if="errors.errors.length" class="form_errors">
        <li v-for="error in errors.errors">{{ error.msg }}</li>
      </ul>
      <div class="col-full">
        <label v-translate>Email</label>
        <input v-if="isLoggedIn" disabled readonly type="email"
               v-model="userProfile.username"
               v-validate.initial="userProfile.username" data-rules="required" data-as="Username"
               :class="{'input': true, 'has-error': errors.has('userProfile.username') }" required>
        <input v-if="!isLoggedIn" type="email"
               v-model="userProfile.username"
               v-validate.initial="userProfile.username" data-rules="required|name_exist|email" data-as="Email"
               :class="{'input': true, 'has-error': errors.has('userProfile.username') }" required>
      </div>
      <div class="clearfix cols" v-if="!isLoggedIn">
        <div class="col-1">
          <label v-translate>fieldPassword</label>
          <input type="password" v-model="userProfile.password"
                 v-validate.initial="userProfile.password" name="userProfile.password" data-rules="required|min:5" :class="{'input': true, 'has-error': errors.has('userProfile.password') }" data-as="Password" required>
        </div>
        <div class="col-2">
          <label v-translate>Confirm Password</label>
          <input type="password" name="userProfile.password_cinfirm" v-model="userProfile.password_cinfirm"
                 v-validate.initial="userProfile.password_cinfirm" data-rules="required|confirmed:userProfile.password" :class="{'input': true, 'has-error': errors.has('userProfile.password_cinfirm') }" data-as="Confirm Password" required>
        </div>
      </div>

      <div class="clearfix cols">
        <div class="col-1">
          <label v-translate>First Name</label>
          <input type="text" v-model="userProfile.firstName"
                 v-validate.initial="userProfile.firstName" data-rules="required" :class="{'has-error': errors.has('userProfile.firstName') }" data-as="First Name" required>
        </div>
        <div class="col-2">
          <label v-translate>Last Name</label>
          <input type="text" v-model="userProfile.lastName"
                 v-validate.initial="userProfile.lastName" data-rules="required" :class="{'has-error': errors.has('userProfile.lastName') }" data-as="Last Name" required>
        </div>
      </div>

      <div class="clearfix">
        <div class="col-1">
          <label class="label-inline">
            <input type="radio" name="maleGender" value="true" v-model="userProfile.maleGender"
                 v-validate.initial="userProfile.maleGender" data-rules="required" :class="{'has-error': errors.has('userProfile.maleGender') }" data-as="Sex">
            <span v-translate>Male</span>
          </label>
          <label class="label-inline">
            <input type="radio" name="maleGender" value="false" v-model="userProfile.maleGender"
                 v-validate.initial="userProfile.maleGender" data-rules="required" :class="{'has-error': errors.has('userProfile.maleGender') }" data-as="Sex">
            <span v-translate>Female</span>
          </label>
        </div>
      </div>

      <div class="clearfix cols">
        <div class="col-1">
          <label v-translate>Phone</label>
          <input type="text" v-model="userProfile.phone"
                 v-validate.initial="userProfile.phone" data-rules="required|numeric" :class="{'has-error': errors.has('userProfile.phone') }" required data-as="Phone">
        </div>
        <div class="col-2">
          <label v-translate>Birthday</label>
          <input type="text" id="birthday" placeholder="YYYY/DD/MM" v-model="userProfile.birthday"
                 v-validate.initial="userProfile.birthday" data-rules="required|birthday_date" :class="{'has-error': errors.has('userProfile.birthday') }" required data-as="Birthday">
        </div>
      </div>
      <button v-on:click.stop.prevent="submitAction" type="submit" v-bind:disabled="errors.errors.length > 0" class="button-primary">{{buttonSave}}</button>

    </form>
  </div>
</template>


<style>
  @import "variables.css";
  .register .form {
    padding-bottom: 0;
  }

  .register.profile .form {
    padding-bottom: 3rem;
  }

  .register .modal-container {
    max-width: 45rem;
    min-height: 23rem;
    height: auto;
  }
  @media (max-width: 480px) {
    .register .modal-container {
      overflow-y: scroll;
      height: 100% !important;
    }
  }

</style>

<script>
  import Modal from './Modal'
  import router from '../router/index'
  import fecha from 'fecha'
  import notification from '../helpers/Notification'

  export default {
    data () {
      return {
        userProfile: {
          username: this.$store.state.user.username || '',
          password: this.$store.state.user.password || '',
          password_cinfirm: this.$store.state.user.password || '',
          firstName: this.$store.state.profile.firstName || '',
          lastName: this.$store.state.profile.lastName || '',
          maleGender: this.$store.state.profile.maleGender || undefined,
          phone: this.$store.state.profile.phone || '',
          birthday: this.$store.state.profile.birthday ? fecha.format(new Date(this.$store.state.profile.birthday), 'YYYY/MM/DD') : '',
          id: this.$store.state.profile.id || '',
          userId: this.$store.state.user.id || ''
        }

      }
    },
    computed: {
      isLoggedIn () {
        return !!this.$store.state.user.username
      },
      dispatchAction () {
        return this.isLoggedIn ? 'updateProfile' : 'registerUserAndReturnPromise';
      },
      buttonSave () {
        return this.isLoggedIn ? 'Update' : 'Register';
      }
    },
    methods: {
      close: function(){
        router.push({name:'date', params: this.$route.params})
      },
      submitAction: function(e) {
        this.$validator.validateAll();

        if (this.errors.any()) {
          e.preventDefault();
          return;
        }
        this.$store.dispatch(this.dispatchAction, this.userProfile).then(() => {
          notification.success(this.t('Success') + '!');
        }).catch((e)=> {
          var serverError = e.exists ? 'This user already exist' : 'Undefined error on server.';
          notification.error(serverError);
        });
      }
    }
  }
</script>



