<template>
  <div id="app" lang="no">
    <app-header></app-header>
    <timeline></timeline>
    <router-view></router-view>
    <loader></loader>
  </div>
</template>

<script>
  require('../assets/css/fonts.css');
  require('../assets/css/main.css');
  require('../assets/css/forms.css');

  import AppHeader from '../components/Sidebar';
  import Timeline from '../components/Timeline';
  import Loader from '../components/Loader';

  export default {
    created () {
      this.showLoader();

      setInterval(() => {
        this.$store.dispatch('getCurrentUser');
      }, 1700000);

      Promise.all([
        this.$store.dispatch('loadCategories'),
        this.$store.dispatch('loadOrganizations'),
        this.$store.dispatch('loadRegions'),
        this.$store.dispatch('loadLevels')
      ]).then(() => {
        this.hideLoader();
      }, () => {
        this.hideLoader();
      })
    },
    components: {
      AppHeader,
      Timeline,
      Loader
    },
    mounted () {
      this.$translate.setLang('no');
    },
    methods: {
      showLoader () {
        if (this.isColdStart()) {
          this.$store.dispatch('setProcessingOn');
        }
      },
      hideLoader () {
        this.$store.dispatch('setProcessingOff');
      },
      isColdStart () {
        return !this.$store.state.events.length && !this.$store.state.categories.length && !this.$store.state.regions.length && !this.$store.state.levels.length && !this.$store.state.organizations.length
      }
    }
  }
</script>



