export default function isInViewport (viewportEl, el, cushion) {
  "use strict";

  let viewport = viewportEl.getBoundingClientRect();
  const rectangle = el.getBoundingClientRect();

  cushion = +cushion || 0;
  viewport = {
    ...viewport,
    top: viewport.top - cushion,
    left: viewport.left - cushion,
    bottom: viewport.bottom + cushion,
    right: viewport.right + cushion
  }

  return !!rectangle
    && rectangle.bottom <= viewport.bottom
    && rectangle.right <= viewport.right
    && rectangle.top >= viewport.top
    && rectangle.left >= viewport.left
}



