<template>
  <form @submit.prevent="submitAction" class="form" id="eventForm" name="eventForm">
    <fieldset>
      <legend v-translate>Information</legend>
      <label v-translate>fieldTitle</label>
      <input v-model="event.title" type="text" v-validate.initial="event.title" v-bind:data-as="t('fieldTitle')"
             data-rules="required" required
             :class="{'has-error': errors.has('event.title') }">
      <div class="clearfix cols">
        <div class="col-1">
          <label v-translate>fieldCategory</label>
          <select v-model="event.categoryId" v-validate.initial="event.categoryId" data-rules="required" required
                  :class="{'has-error': errors.has('event.categoryId') }">
            <option value="" :value="null"><span v-translate>fieldCategory</span></option>
            <optgroup v-for="optgroup in categoryList" v-bind:label="optgroup.name">
              <option v-for="option in optgroup.options" :value="option.id">{{option.name}}</option>
            </optgroup>
          </select>
        </div>
        <div class="col-2">
          <label v-translate>fieldOrganization</label>
          <select v-model="event.organizationId" v-validate.initial="event.organizationId" data-rules="required"
                  required :class="{'has-error': errors.has('event.organizationId') }">
            <option value="" :value="null"><span v-translate>fieldOrganization</span></option>
            <option v-for="option in organizationList" :value="option.id">{{option.name}}</option>
          </select>
        </div>
      </div>
      <div class="clearfix cols">
        <div class="col-1">
          <label v-translate>fieldLevel</label>
          <select v-model="event.levelId" v-validate.initial="event.levelId" data-rules="required" required
                  :class="{'has-error': errors.has('event.levelId') }">
            <option value="" :value="null"><span v-translate>fieldLevel</span></option>
            <option v-for="option in levelList" :value="option.id">{{option.name}}</option>
          </select>
        </div>
        <div class="col-2">
          <label v-translate>fieldRegion</label>
          <select v-model="event.regionId" v-validate.initial="event.regionId" data-rules="required" required
                  :class="{'has-error': errors.has('event.regionId') }">
            <option value="" :value="null"><span v-translate>fieldRegion</span></option>
            <option v-for="option in regionList" :value="option.id">{{option.name}}</option>
          </select>
        </div>
      </div>
      <label v-translate>fieldDescription</label>

      <textarea readonly hidden required v-model="event.description"
                v-validate.initial="event.description" data-rules="required|blank_html|max:600"
                :class="{'has-error': errors.has('event.description') }"></textarea>
      <medium-editor v-model="event.description"></medium-editor>
    </fieldset>
    <fieldset>
      <legend v-translate>Time, Location, Map</legend>
      <label v-translate>helpSetLocationOnMap</label>
      <location-selector-map v-model="geoData"></location-selector-map>
      <location-selector-autocomplete v-model="geoData"></location-selector-autocomplete>
      <input type="hidden" name="latitude" v-model="event.latitude" value="">
      <input type="hidden" name="longitude" v-model="event.longitude" value="">
      <input type="hidden" name="mapZoom" v-model="event.mapZoom" value="">
      <label v-translate>Registration date</label>
      <date-picker v-model="event.registrationDate"></date-picker>
      <div class="clearfix cols">
        <div class="col-1">
          <label v-translate>Start date</label>
          <date-picker v-model="event.startDate"></date-picker>
        </div>
        <div class="col-2">
          <label v-translate>End date</label>
          <date-picker v-model="event.endDate"></date-picker>
        </div>
      </div>


      <label><span v-translate>fieldPositionDescription</span>
        <small v-translate>fieldPositionDescriptionOptional</small>
      </label>
      <textarea readonly hidden v-model="event.geoDescription"
                v-validate.initial="event.geoDescription" data-rules="max:600"
                :class="{'has-error': errors.has('event.geoDescription') }"></textarea>
      <medium-editor v-model="event.geoDescription" ></medium-editor>
    </fieldset>
    <!-- fieldset>
      <legend>Faner</legend>
      <label>FANE  1</label>
      <div class="pull-right">
        <div class="close-group">✕</div>
      </div>
      <div class="clearfix">
        <div class="col-1">
          <label>Fane tittel</label>
          <input v-model="tabName" type="text" name="" placeholder="" class="u-full-width">
        </div>
        <div class="col-2">
          <label>Fane kort tittel</label>
          <input type="text" name="login_name" placeholder="" class="u-full-width">
        </div>
      </div>
      <label>Fane beskrivelse</label>
      <textarea></textarea>
      <button>Add FANE</button>
    </fieldset -->
    <button type="submit" v-translate>buttonSave</button>
  </form>
</template>


<script>
  import map from 'lodash/fp/map'
  import pick from 'lodash/fp/pick'
  import clone from 'lodash/fp/clone'
  import fecha from 'fecha'
  import DatePicker from './DatePicker'
  import MediumEditor from './MediumEditor'
  import Modal from './Modal'
  import LocationSelectorMap from './LocationSelectorMap'
  import LocationSelectorAutocomplete from './LocationSelectorAutocomplete'

  const pickIdAndName = pick(['id', 'name']);
  const getOptions = map(pickIdAndName);

  export default {
    props: {
      value: {
        required: true,
        validator: function (value) {
          return typeof value === 'object';
        }
      }
    },
    components: {
      Modal,
      LocationSelectorMap,
      LocationSelectorAutocomplete,
      MediumEditor,
      DatePicker
    },
    data () {
      return {
        event: {}
      }
    },
    mounted () {
      this.setEventFromValue(this.value);
    },
    computed: {
      organizationList () {
        return getOptions(this.$store.state.organizations)
      },
      levelList () {
        return getOptions(this.$store.state.levels)
      },
      regionList () {
        return getOptions(this.$store.state.regions)
      },
      categoryList () {
        return this.$store.getters.categoriesTree
      },
      geoData: {
        // getter
        get () {
          return {
            latitude: this.event.latitude,
            longitude: this.event.longitude,
            zoom: this.event.mapZoom
          }
        },
        // setter
        set (value) {
          this.event = {
            ...this.event,
            latitude: value.latitude,
            longitude: value.longitude,
            mapZoom: value.zoom
          }
        }
      }
    },
    watch: {
      value (value, oldValue) {
        if(value === oldValue) {
          return;
        }
        this.setEventFromValue(value)
      }
    },
    methods: {
      setEventFromValue(value) {
        this.event = Object.assign({startDate: null, endDate: null}, value, {createdBy: this.$store.state.user.id});
      },
      submitAction(e) {
        this.$validator.validateAll();

        if (this.errors.any()) {
          e.preventDefault();
          return;
        }

        this.$emit('submit', clone(this.event));
      }
    }
  }
</script>



