import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';
import db from '../helpers/Db';
import oneByOne from '../helpers/one-by-one';
import combineEvents from '../helpers/EventsHelper/combine-events';

import notification from '../helpers/Notification';

import isEmpty from 'lodash/fp/isEmpty';
import pick from 'lodash/fp/pick';
import reduce from 'lodash/fp/reduce';
import debounce from 'lodash/fp/debounce';
import sortBy from 'lodash/fp/sortBy';
import map from 'lodash/fp/map';
import min from 'lodash/fp/min';
import max from 'lodash/fp/max';
import filter from 'lodash/fp/filter';
import uniq from 'lodash/fp/uniq';
import union from 'lodash/fp/union';
import keys from 'lodash/fp/keys';

Vue.use(Vuex);

const setLoaderOn = (commit) => {
  'use strict';
  commit('SET_PROCESSING_ON');
};

const setLoaderOff = debounce(500)((commit) => {
  commit('SET_PROCESSING_OFF');
});

const pickSimpleEvent = map(pick(['startDate', 'id', 'categoryId', 'organizationId', 'levelId', 'regionId']))
const mapStartDate = map('startDate');
const sortByStartDate = sortBy('startDate');
const getIds = map('id');

function reducerCategoriesTree (result, value) {
  if (value.parentId === value.id) {
    result[value.id] = Object.assign({}, result[value.id] || {options: []}, value)
  } else {
    (result[value.parentId] || (result[value.parentId] = {options: []})).options.push(value);
  }
  return result;
}

const state = {
  isProcessing: false,
  /* day, week, month */
  displayModeCompact: false,
  timelineMode: 'day', // day, week, month, year
  eventsFilter: {
    organizationId: false,
    levelId: false,
    regionId: false,
    categoryId: false
  },
  flatEvents: [],
  events: {},
  event: {},
  eventsStatus: {
    limitLeft: false,
    loadingLeft: false,
    limitRight: false,
    loadingRight: false
  },
  newEvent: {
    id: null,
    description: null,
    endDate: null,
    startDate: null,
    registrationDate: null,
    title: null,
    geoDescription: null,
    latitude: 59.9138688,
    longitude: 10.752245399999993,
    mapZoom: 12,
    categoryId: null,
    levelId: null,
    organizationId: null,
    regionId: null,
    createdBy: null
  },
  categories: {},
  regions: {},
  levels: {},
  organizations: {},
  user: {},
  profile: {},
  currentDate: new Date().getTime()
};

const getters = {
  eventDates: (state) => {
    const flatKeys = map(parseInt)(keys(state.flatEvents));
    const eventKeys = uniq(mapStartDate(state.events));
    return union(flatKeys)(eventKeys);
  },
  simplifiedEvents: (state) => {
    return sortByStartDate(pickSimpleEvent(state.events));
  },
  outsideEvents: (state) => {
    const outsideTimestamps = mapStartDate(state.events);
    const leftTimestamp = min(outsideTimestamps);
    const rightTimestamp = max(outsideTimestamps);
    return {
      left: {
        timestamp: leftTimestamp,
        ids: getIds(filter(v => v.startDate === leftTimestamp)(state.events))
      },
      right: {
        timestamp: rightTimestamp,
        ids: getIds(filter(v => v.startDate === rightTimestamp)(state.events))
      }
    };
  },
  categoriesTree: (state) => {
    return reduce(reducerCategoriesTree, {})(state.categories);
  }
};

const mutations = {
  SET_PROCESSING_ON (state) {
    state.isProcessing = true;
  },
  SET_PROCESSING_OFF (state) {
    state.isProcessing = false;
  },
  SWITCH_MODE (state) {
    state.displayModeCompact = !state.displayModeCompact;
  },
  SWITCH_COMPACT_MODE_ON (state) {
    state.displayModeCompact = true;
  },
  SWITCH_COMPACT_MODE_OFF (state) {
    state.displayModeCompact = false;
  },
  SWITCH_TIMELINE_MODE (state, data) {
    state.timelineMode = data;
  },
  SWITCH_LOADING_EVENTS_LEFT_ON (state) {
    state.eventsStatus.loadingLeft = true;
  },
  SWITCH_LOADING_EVENTS_LEFT_OFF (state) {
    state.eventsStatus.loadingLeft = false;
  },
  SWITCH_LOADING_EVENTS_RIGHT_ON (state) {
    state.eventsStatus.loadingRight = true;
  },
  SWITCH_LOADING_EVENTS_RIGHT_OFF (state) {
    state.eventsStatus.loadingRight = false;
  },
  SCROLL_TO_DATE_LEFT (state, data) {
    state.eventsStatus.scrollToDateLeft = data;
  },
  SCROLL_TO_DATE_RIGHT (state, data) {
    state.eventsStatus.scrollToDateRight = data;
  },
  LOAD_CATEGORIES (state, data) {
    state.categories = data;
  },
  LOAD_REGIONS(state, data) {
    state.regions = data;
  },
  LOAD_ORGANIZATIONS (state, data) {
    state.organizations = data;
  },
  LOAD_LEVELS (state, data) {
    state.levels = data;
  },
  COMBINE_EVENTS_RIGHT (state, data) {
    state.events = combineEvents.right(state.events, data);
  },
  COMBINE_EVENTS_LEFT (state, data) {
    state.events = combineEvents.left(state.events, data);
  },
  SET_EVENTS_LEFT_LIMIT (state, date) {
    state.eventsStatus.limitLeft = date;
  },
  SET_EVENTS_RIGHT_LIMIT (state, date) {
    state.eventsStatus.limitRight = date;
  },
  LOAD_EVENTS (state, data) {
    state.events = data;
  },
  SAVE_EVENT (state, data) {
    state.events = {...state.events, [data.id]: data}
  },
  REMOVE_EVENT_BY_ID (state, data) {
    let newState = Object.assign({}, state.events);
    delete newState[data];
    state.events = newState;
  },
  FILTER_BY_ORGANIZATION (state, data) {
    state.eventsFilter.organizationId = data || false;
  },
  FILTER_BY_LEVELS (state, data) {
    state.eventsFilter.levelId = data || false;
  },
  FILTER_BY_REGIONS (state, data) {
    state.eventsFilter.regionId = data || false;
  },
  FILTER_BY_CATEGORY (state, data) {
    state.eventsFilter.categoryId = data || false;
  },
  SET_USER (state, data) {
    state.user = data;
  },
  SET_PROFILE (state, data) {
    state.profile = data;
  },
  LOAD_FLAT_EVENTS(state, data) {
    state.flatEvents = data;
  }
}

const actions = {
  setProcessingOn({commit}) {
    commit('SET_PROCESSING_ON');
  },
  setProcessingOff({commit}) {
    commit('SET_PROCESSING_OFF');
  },
  loadCategories({commit}) {
    return db.Categories.getAll()
      .then(function (data) {
        commit('LOAD_CATEGORIES', data)
      })
  },
  loadOrganizations({commit}) {
    return db.Organizations.getAll()
      .then(function (data) {
        commit('LOAD_ORGANIZATIONS', data);
      })
  },
  loadRegions({commit}) {
    return db.Regions.getAll()
      .then(function (data) {
        commit('LOAD_REGIONS', data);
      })
  },
  loadLevels({commit}) {
    return db.Levels.getAll()
      .then(function (data) {
        commit('LOAD_LEVELS', data);
      })
  },
  loadEvents({commit}) {
    return db.Events.getAll()
      .then(function (data) {
        commit('LOAD_EVENTS', data)
      })
  },
  scrollToDate({commit}, data) {
    commit('SCROLL_TO_DATE', data);
  },
  loadEventsLeft({commit}, data) {
    oneByOne(() => {
      commit('SWITCH_LOADING_EVENTS_LEFT_ON');
      return db.Events.getEarlierThan(data.date, data.except)
        .then(function (result) {
          if (isEmpty(result)) {
            commit('SET_EVENTS_LEFT_LIMIT', data.date);
          }
          commit('COMBINE_EVENTS_LEFT', result);
          commit('SWITCH_LOADING_EVENTS_LEFT_OFF');
        })
        .catch(function () {
          "use strict";
          commit('SWITCH_LOADING_EVENTS_LEFT_OFF');
        })
    })
  },
  loadEventsRight({commit}, data) {
    oneByOne(() => {
      commit('SWITCH_LOADING_EVENTS_RIGHT_ON');
      return db.Events.getLaterThan(data.date, data.except)
        .then(function (result) {
          if (isEmpty(result)) {
            commit('SET_EVENTS_RIGHT_LIMIT', data.date);
          }
          commit('COMBINE_EVENTS_RIGHT', result);
          commit('SWITCH_LOADING_EVENTS_RIGHT_OFF');
        })
        .catch(function () {
          "use strict";
          commit('SWITCH_LOADING_EVENTS_RIGHT_OFF');
        })
    })
  },
  loadEvent({commit}, data) {
    return db.Events.getById(data)
      .then((event) => {
        commit('SAVE_EVENT', event)
      });
  },
  saveEvent({commit}, data) {
    setLoaderOn(commit);
    return db.Events.saveEvent(data)
      .then((event) => {
        commit('SAVE_EVENT', event);
        setLoaderOff(commit);
        return event;
      })
      .catch((e) => {
        console.log(e);
        e.message && notification.error(e.message);
        setLoaderOff(commit);
        return Promise.reject(e);
      });
  },
  removeEventById({commit}, data) {
    setLoaderOn(commit);
    db.Events.removeEventById(data)
      .then(() => {
        commit('REMOVE_EVENT_BY_ID', data);
        setLoaderOff(commit);
      })
      .catch((e) => {
        setLoaderOff(commit);
        if (e.message) {
          notification.error(e.message);
        }
        return Promise.reject(e);
      });
  },
  saveSubscription ({commit}, data) {
    setLoaderOn(commit);
    return db.Subscriptions.saveSubscription(data)
      .then((event) => {
        setLoaderOff(commit);
        return event;
      })
      .catch((e) => {
        setLoaderOff(commit);
        if (e.message) {
          notification.error(e.message);
        }
        return Promise.reject(e);
      });
  },
  switchMode ({commit}) {
    commit('SWITCH_MODE');
  },
  switchTimelineMode ({commit}, mode) {
    commit('SWITCH_TIMELINE_MODE', mode);
  },
  switchFilterByOrganization ({commit}, mode) {
    commit('FILTER_BY_ORGANIZATION', mode);
  },
  switchFilterByLevel ({commit}, mode) {
    commit('FILTER_BY_LEVELS', mode);
  },
  switchFilterByRegion ({commit}, mode) {
    commit('FILTER_BY_REGIONS', mode);
  },
  switchFilterByCategory ({commit}, mode) {
    commit('FILTER_BY_CATEGORY', mode);
  },
  loginUserAndReturnPromise ({commit}, data) {
    setLoaderOn(commit);

    return db.User.login(data.login, data.password)
      .then((user) => {
        setLoaderOff(commit);
        commit('SET_USER', user);
        return db.User.getProfile()
          .then((data) => {
            commit('SET_PROFILE', data);
          });
      })
      .catch((e) => {
        setLoaderOff(commit);
        return Promise.reject(e);
      });
  },
  registerUserAndReturnPromise ({commit}, data) {
    setLoaderOn(commit);
    return db.User.register({
      username: data.username,
      password: data.password,
      enabled: true
    })
      .then(() => {
        var userData = {
          login: data.username,
          password: data.password
        }
        return db.User.login(userData.login, userData.password)
      })
      .then((user) => {
        commit('SET_USER', user)
        return db.User.createProfile(user.id, {
          firstName: data.firstName,
          lastName: data.lastName,
          maleGender: data.maleGender,
          phone: data.phone,
          birthday: new Date(data.birthday).getTime()
        })
          .then(() => {
            return db.User.getProfile()
              .then((data) => {
                commit('SET_PROFILE', data)
                setLoaderOff(commit);
              })
          })
      });
  },
  updateProfile ({commit}, data) {
    setLoaderOn(commit);
    return db.User.updateProfile({
      firstName: data.firstName,
      lastName: data.lastName,
      maleGender: data.maleGender,
      phone: data.phone,
      birthday: new Date(data.birthday).getTime(),
      id: data.id,
      userId: data.userId
    })
      .then((data) => {
        commit('SET_PROFILE', data)
        setLoaderOff(commit);
      });
  },
  logoutUser ({commit}) {
    setLoaderOn(commit);
    db.User.logout()
      .then(() => {
        setLoaderOff(commit);
        commit('SET_USER', {});
        commit('SET_PROFILE', {});
      })
      .catch((e) => {
        setLoaderOff(commit);
        return e;
      });
  },
  getCurrentUser ({commit}) {
    db.User.getCurrentUser()
      .then((user) => {
        commit('SET_USER', user);
      })
      .catch(() => {
        commit('SET_USER', {});
        commit('SET_PROFILE', {});
      });
  },
  loadFlatEvents({commit}, data) {
    return db.Events.getFlatEvents(data)
      .then(function (data) {
        commit('LOAD_FLAT_EVENTS', data)
      })
  }
}

const store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  strict: true, //process.env.NODE_ENV === 'production',
  plugins: [createPersistedState({
    key: 'ncfc-java',
    paths: Object.keys(state).filter(v => v !== 'events' && v != 'eventsStatus' && v !== 'currentDate')
  })]
});

export default store



