import VeeValidate, {Validator} from 'vee-validate';
import db from './Db'

function debounce(func, wait, immediate) {
  var timeout;
  return function () {

    var context = this, args = arguments;

    return new Promise(function (resolve) {
      var later = function () {
        timeout = null;
        if (!immediate) resolve(func.apply(context, args));
      };

      var callNow = immediate && !timeout;
      clearTimeout(timeout);
      timeout = setTimeout(later, wait);

      if (callNow) resolve(func.apply(context, args));
    });
  };
};

function isNameExists(name) {
  if (!name) {
    return Promise.resolve([{valid: true}]);
  }

  return new Promise(function (resolve) {
    db.User.checkExist(name)
      .then((response) => {
        resolve([{valid: !response.exists}]);
      })
      .catch(() => {
        resolve(false);
      });
  });
}

const isNameExistsDebounced = debounce(isNameExists, 400);

function isEmptyText(s) {
  const div = document.createElement('div');
  div.innerHTML = s;
  return !div.innerText.trim();
}
const isNotEmptyText = s => !isEmptyText(s);

function isValidDate(date) {
  const temp = date.split('/');
  const d = new Date(temp[0] + '/' + temp[1] + '/' + temp[2]);
  return (d && (d.getMonth() + 1) == temp[1] && d.getDate() == Number(temp[2]) && d.getFullYear() == Number(temp[0]));
}

Validator.extend('blank_html', {
  getMessage: field => `Field "${field}" can not be empty`,
  validate: isNotEmptyText
});

Validator.extend('name_exist', {
  getMessage: field => `This Email is already exists in system`,
  validate: isNameExistsDebounced
});

Validator.extend('birthday_date', {
  getMessage: field => `Invalid date format for ${field}`,
  validate: isValidDate
});

export default VeeValidate;



