<template>
 <div>{{categoryName}}</div>
</template>

<script>
  import get from 'lodash/fp/get';

  const getName = get('name');

  var eventTitles = {
    'treningssamlinger': 'Trenings\u00ADsamlinger',
    "pressekonferanser":'Presse\u00ADkonferanser',
    'uci kongress':'Uci\u00AD kongress'
  };

  function eventHyhenator(name) {
    return eventTitles[name.toLowerCase()] || name;
  }

  export default {
    props: ['categoryId'],
    computed: {
      category: function(){
        return this.$store.state.categories[this.categoryId] || undefined;
      },
      categoryName: function(){
        return this.category ? eventHyhenator(getName(this.category)) : '';
      }
    }
  }
</script>



