import config from '../../config'

import flow from 'lodash/fp/flow';
import isEmpty from 'lodash/fp/isEmpty'
import map from 'lodash/fp/map';
import pick from 'lodash/fp/pick';
import sortBy from 'lodash/fp/sortBy'
import take from 'lodash/fp/take'
import takeRight from 'lodash/fp/takeRight'
import pickBy from 'lodash/fp/pickBy'

const takeLimitLeft = take(config.events_limit);
const takeLimitRight = takeRight(config.events_limit);

const getIdsInTimelineOrder = flow(map(pick(['id', 'startDate'])), sortBy('startDate'), map('id'))

export function combineLeft(events, data) {
  if (isEmpty(events)) {
    return data;
  }
  const combinedEvents = Object.assign({}, events, data);
  const filteredIds = takeLimitLeft(getIdsInTimelineOrder(combinedEvents));
  return pickBy(v => filteredIds.indexOf(v.id) > -1)(combinedEvents);
}

export function combineRight(events, data) {
  if (isEmpty(events)) {
    return data;
  }
  const combinedEvents = Object.assign({}, events, data);
  const filteredIds = takeLimitRight(getIdsInTimelineOrder(combinedEvents));
  return pickBy(v => filteredIds.indexOf(v.id) > -1)(combinedEvents);
}

export default {
  left: combineLeft,
  right: combineRight
}



