<template>
  <div class="form">
    <div class="user-profile clearfix">
      <i class="user-profile_avatar icon icon-user"></i>
      <h4 class="user-profile_name" v-if="$store.state.user.username">
        <span v-translate>Hello</span>, {{$store.state.profile.firstName}}!
      </h4>
      <router-link :to="{ name: 'useredit', params: routeParams}" class="edit-user-link" v-translate>Edit profile</router-link>
      <div class="user-profile_email" v-if="$store.state.user.email">{{$store.state.user.email}}</div>
    </div>
    <button v-on:click="submitAction" class="button-primary" v-translate>Log out</button>
  </div>
</template>

<style>
  .user-profile {
    border-bottom:1px dotted #ccc;
    margin-bottom:2rem;
    padding-bottom:1rem;
  }
  .user-profile_avatar {
    float: left;
    display: block;
    width: 5rem;
    height: 5rem;
    line-height:4.7rem;
    font-size: 3rem;
    text-align: center;
    padding: 0;
    border: 1px solid #ccc;
    border-radius: 50%;
    margin-right: 1.5rem;
    color:#999;
    background: #f1f1f1;
  }
  .user-profile_name {
    margin: 0;
    padding-top: 0.2rem;
  }
  .user-profile_email {
    color: #999;
    font-size:1.3rem;
    margin-bottom:2rem;
  }
  .edit-user-link {
    color: #2d67bb;
    font-size: 1.3rem;
  }
  .login.profile .form {
    padding-bottom: 2rem;
  }
</style>

<script>
  import notification from '../helpers/Notification'

  export default {
    computed: {
      routeParams () {
        return this.$store.state.route.params;
      }
    },
    methods: {
      submitAction: function(e) {
        this.$store.dispatch('logoutUser');
        notification.info(this.t('LoggedOut'));
        return;
      }
    }
  }
</script>






