<template>
  <img v-if="organizationImage" class="organization-image" v-bind:src="src" v-bind:style="organizationStyle" v-bind:alt="organizationName"
       v-bind:title="organizationName"/>
</template>

<style>
  .organization-image {
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: contain;
  }
</style>

<script>
  import config from '../config';
  import find from 'lodash/fp/find';
  import get from 'lodash/fp/get';
  import memoize from 'lodash/memoize';

  const getThumbnailId = memoize(get('attachmentId'), v => v.id);
  const getName = memoize(get('name'), v => v.id);

  export default {
    props: {
      organizationId: {
        default: undefined,
        validator (value) {
          return typeof value === 'number'
        }
      },
      size: {
        type: Number,
        default: 100,
        validator (value) {
          return [50, 100, 200].indexOf(value) > -1
        }
      }
    },
    data () {
      return {
        src: require('../assets/img/1p.gif')
      }
    },
    computed: {
      organization () {
        return this.$store.state.organizations[this.organizationId] || { id: 0 }
      },
      organizationStyle () {
          return {
              backgroundImage: `url(${this.organizationImage})`
          }
      },
      organizationImage () {
        const thumbnailId = getThumbnailId(this.organization);

        if (!thumbnailId) {
          return require('../assets/img/logo-grey.svg');
        }
        return `${config.host}/attachments/${thumbnailId}?size=${this.size}`;
      },
      organizationName () {
        return getName(this.organization);
      }
    }
  }
</script>




