import request from '../HttpRequest'
import defaultReject from '../HttpRequest/default-reject'
import config from '../../config'

import keyBy from 'lodash/fp/keyBy';
import reject from 'lodash/fp/reject';

const keyById = keyBy('id');
const rejectDeleted = reject('deleted');

function EventClass() {

  function getEvents() {
    return request.get('/events')
      .then(rejectDeleted)
      .then(keyById)
      .catch(defaultReject);
  }

  function getLaterThan(date, excludedIds = []) {
    return request.post('/events/start_date_search', {
      startDate: date,
      excludedIds,
      limit: config.events_load,
      before: false
    })
      .then(keyById)
      .catch(defaultReject);
  }

  function getEarlierThan(date, excludedIds = []) {
    return request.post('/events/start_date_search', {
      startDate: date,
      excludedIds,
      limit: config.events_load,
      before: true
    })
      .then(keyById)
      .catch(defaultReject);
  }

  function getEventById(id) {
    return request.get('/events/' + id)
      .catch(defaultReject);
  }

  function saveEvent(data) {
    if (data.id) {
      return request.put('/events/' + data.id, data)
        .catch(defaultReject);
    }

    return request.post('/events', data)
      .catch(defaultReject);
  }

  function removeEventById(id) {
    return request.delete('/events/' + id)
      .catch(defaultReject);
  }

  function getFlatEvents(data){
    return request.get('/events/' + 'range?from='+ data.start + '&to=' + data.end)
      .catch(defaultReject);
  }


  this.getAll = getEvents;
  this.getEarlierThan = getEarlierThan;
  this.getLaterThan = getLaterThan;

  this.saveEvent = saveEvent;

  this.removeEventById = removeEventById;

  this.getById = getEventById;

  this.getFlatEvents = getFlatEvents;

}

export default EventClass



