<template>
  <in-viewport v-if="container" :handler="handler" :should-handle="shouldHandle"
               :scroll-container="container"></in-viewport>
</template>
<script>
  import fecha from 'fecha'
  import isEqual from 'lodash/fp/isEqual'
  import inViewport from './InViewport'

  const eventUpdate = new Event("update", {
    cancelable: true
  })

  const TimeEventLoader = {
    name: 'time-event-loader',
    components: {
      inViewport
    },
    props: {
      direction: {
        type: String,
        required: true
      },
      scrollContainer: {
        type: String,
        required: true
      }
    },
    data () {
      return {
        container: false
      }
    },
    mounted() {
      this.handler = this.direction === 'left' ? this.loadEventsLeft : this.loadEventsRight;
      this.container = this.$parent.$refs[this.scrollContainer]
    },
    computed: {
      shouldHandle () {
        return !this.$store.state.eventsStatus[this.direction === 'left' ? 'loadingLeft' : 'loadingRight'];
      }
    },
    methods: {
      loadEventsLeft () {
        let leftSideData = this.$store.getters.outsideEvents.left;

        if (!leftSideData.timestamp) {
          leftSideData = {
            timestamp: fecha.parse(this.$store.state.route.params.date, 'YYYY-MM-DD').getTime(),
            ids: []
          }
        }

        if (!leftSideData.timestamp || this.isOutsideLeft(leftSideData.timestamp)) {
          return;
        }

        this.$store.dispatch('loadEventsLeft', {
          date: leftSideData.timestamp,
          except: leftSideData.ids
        });
      },
      loadEventsRight () {
        let rightSideData = this.$store.getters.outsideEvents.right;

        if (!rightSideData.timestamp) {
          rightSideData = {
            timestamp: fecha.parse(this.$store.state.route.params.date, 'YYYY-MM-DD').getTime(),
            ids: []
          }
        }

        if(rightSideData.timestamp && this.isOutsideRight(rightSideData.timestamp)) {
          return;
        }

        this.$store.dispatch('loadEventsRight', {
          date: rightSideData.timestamp,
          except: rightSideData.ids
        });
      },
      isOutsideLeft (timestamp) {
        return this.$store.state.eventsStatus.limitLeft > 0 && this.$store.state.eventsStatus.limitLeft >= timestamp
      },
      isOutsideRight (timestamp) {
        return this.$store.state.eventsStatus.limitRight > 0 && this.$store.state.eventsStatus.limitRight <= timestamp
      }
    },
    watch: {
      '$store.state.events' (value, oldValue) {
        if (isEqual(value)(oldValue)) {
          return;
        }
        this.$nextTick(() => {
          this.container && this.container.dispatchEvent(eventUpdate);
        });
      }
    }
  }

  export default TimeEventLoader
</script>



