import request from '../HttpRequest'
import map from 'lodash/fp/map'

const mapByAuthority = map('authority');

function UserClass() {
  function prepareUserRoles(user) {
    user.authorities = mapByAuthority(user.authorities);
    return user;
  }

  this.getCurrentUser = function () {
    return request.get('/users/user')
      .then(user => {
        if (!user || user.message === 'USER_IS_NOT_AUTHORIZED') {
          return Promise.reject();
        }

        return prepareUserRoles(user);
      })
      .catch(e => {
        e && console.warn(e);
        return Promise.reject(new Error('User is not logged in'));
      });
  }

  this.login = function (username, password) {
    return request.post('/authenticate/login', {
      username,
      password
    })
      .then((r)=>{
        return this.getCurrentUser();
      })
  };

  this.register = function (data) {
    return request.post('/users', data);
  };

  this.updateProfile = function (data) {
    return request.put('/users/' + data.userId + '/profiles/' + data.id, data);
    //For Update Profile
    // return request.post('/users/' + data.userId + '/profiles', data)
  };

  this.createProfile = function (id, data) {
    return request.post('/users/' + id + '/profiles', data)
  };

  this.checkExist = function (username) {
    return request.get('/users/exists?username='+ encodeURIComponent(username));

  };

  this.getProfile = function () {
    return request.get('/profiles/current');
  };

  this.logout = function () {
    return request.post('/authenticate/logout');
  }

  this.isUserEnroll = function (data) {
    return request.get('/enrollments/' + data.eventId + '/users/' + data.userId + '/exists');
  }
}

export default UserClass;



