<template>
  <main class="timeline" v-smooth-scroll
          v-bind:class="{ 'compact-mode': classCompactMode, 'timeline__loading': isLoadingEventsLeft || isLoadingEventsRight }"
        ref="viewport">
    <timeline-event-loader direction="left" scroll-container="viewport"></timeline-event-loader>
    <component v-for="(event,index) in events" v-bind:is="event.component" v-bind:event="event.data"
               v-bind:ref="event.component"></component>
    <timeline-event-loader direction="right" scroll-container="viewport"></timeline-event-loader>
  </main>
</template>

<style>
  @import "variables.css";

  .timeline {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: flex-start;
    box-sizing: border-box;
    height: calc(100vh);
    padding: var(--timelineTopPadding) 0 0 2rem;
    overflow: hidden;
    overflow-x: scroll;
    margin: 0 0 0 20rem;
    background: linear-gradient(to bottom, #4a90e2 0%, #4a90e2 100%);
    background-size: 3px 3px;
    background-repeat: repeat-x;
    background-position: 0 3rem;
  }

  .timeline__loading {
    overflow-x: hidden;
    opacity: .5;
  }

  .timeline__loading * {
    pointer-events: none !important;
  }

  @media (--header-small) {
    .timeline {
      height: calc(100vh - var(--headerHeightSm));
    }

  }

  /* Vertical layout for timeline */
  @media (--timeline-small) {
    .timeline {
      display: block;
      padding: 2rem 0 2em 4.2rem;
      margin-left: 0;
      overflow-x: hidden;
      overflow-y: auto;
      background-repeat: repeat-y;
      background-position: 2rem 0;
    }

    .timeline {
      margin-left: 0;
    }

  }
</style>

<script>
  import fecha from 'fecha';
  import debounce from 'lodash/debounce';
  import throttle from 'lodash/throttle';
  import pickBy from 'lodash/fp/pickBy';
  import isNumber from 'lodash/fp/isNumber';
  import filter from 'lodash/fp/filter';
  import minBy from 'lodash/fp/minBy';
  import maxBy from 'lodash/fp/maxBy';

  import router from '../router/index'
  import isInViewport from '../helpers/in-viewport'
  import createTimelineArray from '../helpers/EventsHelper/create-timeline-array'
  import TimelineEvent from './TimelineEvent'
  import TimelineSeparator from './TimelineSeparator'
  import TimelineEventLoader from './TimelineEventLoader'
  import SmoothScroll from '../directives/SmoothScroll'
  import weekNumber from '../helpers/week-number'
  import smoothScroll from '../helpers/smooth-scroll'

  const pickByNumber = pickBy(isNumber)

  const minByEventDate = minBy(x => x.event.date)
  const maxByEventDate = maxBy(x => x.event.date)

  function getSpeedByOffset(offset) {
    const distance = Math.abs(offset.top) > Math.abs(offset.left) ? offset.top : offset.left;
    let speed = Math.round(Math.abs(distance) / 25);
    if (speed <= 100) speed = 100;
    if (speed >= 1000) speed = 1000;
    return speed;
  }

  export default {
    components: {
      TimelineEvent,
      TimelineSeparator,
      TimelineEventLoader
    },
    directives: {
      SmoothScroll
    },
    data () {
      return {
        isScrollManual: false,
        isScrollAuto: false,
        classDisableHover: false,
        anchoredEventPosition: false,
        anchoredEventId: false
      }
    },
    mounted () {
      this.$refs.viewport.addEventListener('scroll', (() => {
        return debounce(() => {
          this.isScrollManual = true
        }, 400, {
          leading: true,
          trailing: false
        })
      })(), false);
      this.$refs.viewport.addEventListener('scroll', (() => {
        return debounce(() => {
          this.isScrollManual = false;
          this.$nextTick(this.changeRouteDate);
        }, 400, {
          leading: false,
          trailing: true
        })
      })(), false);
      this.$refs.viewport.addEventListener('scroll', (() => {
        return throttle(this.changeRouteDate, 300, {
          leading: true,
          trailing: true
        })
      })(), false);
    },
    methods: {
      getFirstVisibleTimelineHeader () {
        const visibleTimelineHeaders = this.$refs['timeline-separator'].filter(v => isInViewport(this.$el, v.$el, 150));
        return minByEventDate(visibleTimelineHeaders);
      },
      changeRouteDate: function () {
        if (this.isScrollAuto || this.isLoadingEventsLeft || this.isLoadingEventsRight || this.$route.name !== 'date') {
          return;
        }

        const visibleTimelineHeader = this.getFirstVisibleTimelineHeader();
        if(!visibleTimelineHeader){
          return;
        }
        const routeSlug = fecha.format(visibleTimelineHeader.event.date, 'YYYY-MM-DD');

        if (routeSlug && this.$route.params.date !== routeSlug) {
          router.replace({name: 'date', params: {date: routeSlug}});
        }
      },
      getSeparatorByDate (date) {
        const dateTimestamp = new Date(date);

        const separators = this.$children.filter(v => v.$options._componentTag === 'timeline-separator' && v.event.date <= dateTimestamp);

        if (separators.length == 0) {
          return;
        }

        return maxByEventDate(separators).$el;
      },
      getScrollOffsetViewport (component) {
        let direction = {
          left: 0,
          top: 0
        };

        if (!component) {
          return direction;
        }

        const viewportRect = this.$refs.viewport.getBoundingClientRect();
        const eventRect = component.getBoundingClientRect();

        direction.left = eventRect.left - viewportRect.left;
        direction.top = eventRect.top - viewportRect.top;

        return direction
      },
      smoothScrollViewport (offset, duration) {
        this.isScrollAuto = true;
        return smoothScroll(this.$refs.viewport, offset, duration)
          .then((e) => {
            this.isScrollAuto = false;
          })
          .catch((e) => {
            this.isScrollAuto = false;
          });
      }
    },
    computed: {
      classCompactMode () {
        return this.$store.state.displayModeCompact
      },
      isLoadingEventsLeft () {
        return this.$store.state.eventsStatus.loadingLeft
      },
      isLoadingEventsRight () {
        return this.$store.state.eventsStatus.loadingRight
      },
      filterByEventedFilter () {
        return filter(pickByNumber(this.$store.state.eventsFilter))
      },
      events () {
        const filteredEvents = this.filterByEventedFilter(this.$store.getters.simplifiedEvents);
        return createTimelineArray(filteredEvents, this.$store.state.timelineMode);
      }
    },
    watch: {
      'events' () {
        this.$nextTick(() => {
          const offset = this.getScrollOffsetViewport(this.getSeparatorByDate(this.$store.state.route.params.date));
          this.smoothScrollViewport(offset, 0);
        });
      },
      '$store.state.route.params.date' (date) {
        if (this.isScrollManual || this.$store.state.route.params.id) {
          return;
        }

        const offset = this.getScrollOffsetViewport(this.getSeparatorByDate(date));
        this.smoothScrollViewport(offset, getSpeedByOffset(offset)).then(() => {
          console.log('Highlight all events', date)
        })
      }
    }
  }
</script>



