<script>
  import debounce from 'lodash/debounce';
  import throttle from 'lodash/throttle';

  export default {
    inserted (el) {
      let isHoverDisabled = false;
      const elClass = 'disable-hover';
      const regexClass = new RegExp("(\\s|^)" + elClass + "(\\s|$)");

      const disableHoverOn = function disableHoverOn() {
        if (isHoverDisabled) {
          return;
        }
        isHoverDisabled = true;
        if (!el.className.match(regexClass)) {
          el.className += " " + elClass;
        }
      };

      const disableHoverOff = debounce(function () {
        if (!isHoverDisabled) {
          return;
        }
        isHoverDisabled = false;
        el.className = el.className.replace(regexClass, '');
      }, 300);

      el.addEventListener('scroll', (() => {
        return throttle(() => {
          disableHoverOn();
          disableHoverOff();
        }, 300)
      })(), false);
    }
  }
</script>



